.PokemonInfo{
    text-align: center;
    width: 100%;
    height: 100vh;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.PokemonInfo_main-img{
    min-width: 150px;
    min-height: 150px;
}

.PokemonInfo__content{
    border: 1px solid rgba(0, 0, 0, .5);
    box-shadow: 0 0 5px rgba(0, 0, 0, .5);
    padding: 0 0 20px;
}

@media (max-width:700px){
    .PokemonInfo__content{
        width: 90%;
    }	
}

.PokemonInfo__name{
    font-weight: bold;
    color: #BD3736;
}

.PokemonInfo h5{
    color: #BD3736;
}