@import url(https://fonts.googleapis.com/css?family=Audiowide&display=swap);


.Home{
    background-image: url(/pokemonReact/static/media/background.b6bef951.jpg);
    background-size: cover;
    height: 100vh;
    background-position: center;
    /* filter: blur(5px); */
}


.Home__container{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background: rgba(7, 7, 7, .5);
}

.Home__content{
    /* background-color: rgba(0, 0, 0, .5); */
    padding: 50px;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
}

.Home__title{
text-align: center;
margin-bottom: 20px;
/* font-weight: bold; */
}

.Home__link{
    text-decoration: none;
    border: none;
    position: relative; 
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
}

.Home__link:hover{
    text-decoration: none;
    border: none;
}

.Home__link-letter{
    background: rgba(0, 0, 0, 0.5);
    border: 5px solid rgba(0, 0, 0, .5);
    width: 100px;
    height: 100px;
    border-radius: 50%;
    /* border: 5px solid rgba(0, 0, 0, .5); */
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}


.Home__link figure{
    position: absolute;
    display: flex;
    width: 100%;
    justify-content: center;
    left: 0;
    top: 0;
}

.Home__pokebola{
    
    width: 100px;
    -webkit-transition: .5s;
    transition: .5s;
    border-radius: 50%;
    border: 5px solid rgba(0, 0, 0, .5);
    -webkit-animation: pokeBorde infinite  2s;
            animation: pokeBorde infinite  2s;
}

.Home__pokebola:hover{
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    background: rgba(255, 0, 0, 0.336);
    /* animation: 1 ; */
    opacity: 0;
}


@-webkit-keyframes  pokeBorde{
    0%{
        border: 5px solid rgba(0, 0, 0, .5);
    }
    30%{
        border: 5px solid rgba(245, 73, 73, 0.5);
    }
    60% {
        border: 5px solid rgba(255, 255, 255, .5);
    }
}


@keyframes  pokeBorde{
    0%{
        border: 5px solid rgba(0, 0, 0, .5);
    }
    30%{
        border: 5px solid rgba(245, 73, 73, 0.5);
    }
    60% {
        border: 5px solid rgba(255, 255, 255, .5);
    }
}
.PageLoading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    width: 100%;
  }
  
  
.lds-grid {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
  }
  
  .lds-grid div {
    position: absolute;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #000;
    -webkit-animation: lds-grid 1.2s linear infinite;
            animation: lds-grid 1.2s linear infinite;
  }
  
  .lds-grid div:nth-child(1) {
    top: 6px;
    left: 6px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
  }
  
  .lds-grid div:nth-child(2) {
    top: 6px;
    left: 26px;
    -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
  }
  
  .lds-grid div:nth-child(3) {
    top: 6px;
    left: 45px;
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
  }
  
  .lds-grid div:nth-child(4) {
    top: 26px;
    left: 6px;
    -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
  }
  
  .lds-grid div:nth-child(5) {
    top: 26px;
    left: 26px;
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
  }
  
  .lds-grid div:nth-child(6) {
    top: 26px;
    left: 45px;
    -webkit-animation-delay: -1.2s;
            animation-delay: -1.2s;
  }
  
  .lds-grid div:nth-child(7) {
    top: 45px;
    left: 6px;
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
  }
  
  .lds-grid div:nth-child(8) {
    top: 45px;
    left: 26px;
    -webkit-animation-delay: -1.2s;
            animation-delay: -1.2s;
  }
  
  .lds-grid div:nth-child(9) {
    top: 45px;
    left: 45px;
    -webkit-animation-delay: -1.6s;
            animation-delay: -1.6s;
  }
  
  @-webkit-keyframes lds-grid {
  
    0%,
    100% {
      opacity: 1;
    }
  
    50% {
      opacity: 0.5;
    }
  }
  
  @keyframes lds-grid {
  
    0%,
    100% {
      opacity: 1;
    }
  
    50% {
      opacity: 0.5;
    }
  }
