#MenuRight{
    width: 100%;
    height: 100%;
    /* background: red; */
    position: relative;
    padding: 10px;

}

#MenuRight ul{
    position: fixed;
    /* left: 0; */
    list-style: none;
    padding: 0px;
    margin: 0;
    width: calc(100%); 
    max-height: 90vh;
    overflow: hidden;
    overflow-y:scroll;
    /* left: 0; */
}

#MenuRight li{
    /* padding: 10px; */
    border: 1px solid rgba(0,0,0,.5);
    margin-bottom: 5px;

}

#MenuRight p:hover{
    background: #BD3736;
    color: white
}

#MenuRight p{
    margin: 0;
    width: 100%;
    height: 100%;
    padding: 10px;
    cursor: pointer;
    -webkit-transition: .5s;
    transition: .5s;
}

#MenuRight .MenuRight__selected{
    background: #BD3736;
    color: white
}


@media(min-width: 767px){

    .MenuRight__toggle{
        height: 0px;
        width: 0px;
    }
    #MenuRight{
        top: inherit;
    }

}


@media(max-width: 766px){
    #MenuRight ul{
        position: relative;
    }

    #MenuRight{
        position: fixed;
        z-index: 10000;
        background-color: rgba(255,255,255,.9);
        
    }
}

.MenuRight__Top{
    -webkit-transition: .5s;
    transition: .5s;
   top:5%;
}

.MenuRight__toggle{
    position: fixed;
    top: 0;
    left: 80%;
    padding: 10px;

    border: 1px solid transparent;
}
.MenuRight__toggle:hover{
    color: #BD3736;
    cursor: pointer;
    
}

