

.Home{
    background-image: url('../../images/background.jpg');
    background-size: cover;
    height: 100vh;
    background-position: center;
    /* filter: blur(5px); */
}


.Home__container{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background: rgba(7, 7, 7, .5);
}

.Home__content{
    /* background-color: rgba(0, 0, 0, .5); */
    padding: 50px;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
}

.Home__title{
text-align: center;
margin-bottom: 20px;
/* font-weight: bold; */
}

.Home__link{
    text-decoration: none;
    border: none;
    position: relative; 
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
}

.Home__link:hover{
    text-decoration: none;
    border: none;
}

.Home__link-letter{
    background: rgba(0, 0, 0, 0.5);
    border: 5px solid rgba(0, 0, 0, .5);
    width: 100px;
    height: 100px;
    border-radius: 50%;
    /* border: 5px solid rgba(0, 0, 0, .5); */
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}


.Home__link figure{
    position: absolute;
    display: flex;
    width: 100%;
    justify-content: center;
    left: 0;
    top: 0;
}

.Home__pokebola{
    
    width: 100px;
    transition: .5s;
    border-radius: 50%;
    border: 5px solid rgba(0, 0, 0, .5);
    animation: pokeBorde infinite  2s;
}

.Home__pokebola:hover{
    transform: rotate(90deg);
    background: rgba(255, 0, 0, 0.336);
    /* animation: 1 ; */
    opacity: 0;
}


@keyframes  pokeBorde{
    0%{
        border: 5px solid rgba(0, 0, 0, .5);
    }
    30%{
        border: 5px solid rgba(245, 73, 73, 0.5);
    }
    60% {
        border: 5px solid rgba(255, 255, 255, .5);
    }
}